@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@300&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

*{margin:0;padding:0;box-sizing:border-box}html{font-size:62.5%}body{font-size:1.8rem;background-color:#36393f;color:#fff;font-family:'Roboto', sans-serif;font-weight:300}a{text-decoration:none;color:#fff}img{max-width:100%;transition:all 0.3s ease 0s, height 0s ease 0s}img:hover{transform:scale(1.05)}h4{font-weight:300;margin:1rem;font-size:3rem}h3{font-weight:300;margin:1rem}@media (max-width: 450px){h4{font-size:2.3rem}}.navBar{width:20%;display:flex;justify-content:space-between;align-items:center;margin-right:1rem}.navBar a svg{color:#fff;font-size:3rem}.navDetail{width:40%;margin:3rem auto 7rem;display:flex;justify-content:space-between;font-weight:bold}@media (max-width: 450px){.navBar{position:fixed;bottom:0;background-color:black;width:100%;z-index:1;margin:0;left:0;height:6rem;padding:0 3rem}}.title-arrow{color:#2c5d86;font-size:3rem;vertical-align:bottom}.title-category:hover{opacity:0.8}.img-container{overflow:hidden}.search{display:flex;justify-content:left;align-items:center;margin:1rem}.search svg{font-size:3rem;color:#fff;margin-right:1rem;font-weight:300}.search input{background-color:transparent;border:none;color:#fff;font-size:2.5rem}.cards-container{display:flex;flex-wrap:wrap;max-width:95%;margin:2rem auto;margin-bottom:2rem}.card-container{display:flex;flex-direction:column;width:27rem;padding:0px 4px;margin-bottom:20px}.card-container .img-null{width:100%;background-color:#23272a;padding-top:150%;position:relative}.card-container .img-null svg{position:absolute;top:50%;left:50%;transform:translate(-50%, -50%);width:45px;color:#dcddde}.card-container .img-null-credits{width:100%;background-color:#23272a;padding-top:150%;position:relative}.card-container .img-null-credits svg{position:absolute;top:50%;left:50%;transform:translate(-50%, -50%);width:45px;color:#dcddde}@media (max-width: 450px){.card-container{max-width:100%;margin:0 auto}.card-container img:hover{transform:scale(1.01)}}.paginacion{margin:2rem;text-align:center}.paginacion .active{background-color:grey}.paginacion .pagina{margin:.5rem}.paginacion button{border:none;background-color:transparent;cursor:pointer;color:white;width:40px;height:40px;border-radius:50%}.paginacion button:hover{background-color:grey}.hero{height:80vh;background-position:center;background-size:cover}@media (max-width: 450px){.hero{height:40vh;background-position:center;background-size:cover}}.info-container{width:70%;display:flex;justify-content:space-between;margin:10rem 3rem}.info-container .info-img{max-width:35%;margin-right:2rem;height:100%}.info-container .item-info{width:60%}.info-container .item-info h4{margin:0 0 2rem 0}.info-container p{margin:2rem 0}.info-container svg,.info-container a{color:#2196f3}.video-container{width:90%;margin:1rem auto}.video{margin:1rem;border:none}.seasons{position:relative;width:-webkit-max-content;width:-moz-max-content;width:max-content}.seasons .season-select{margin:2rem;padding:1rem 2rem;border:#fff;background-color:#23272a;color:#fff;border-radius:7px;-webkit-appearance:none;-moz-appearance:none;appearance:none;font-size:1.8rem;width:16rem;cursor:pointer}.seasons::after{content:"";position:absolute;display:block;width:7px;height:7px;top:50%;right:36px;transform:translateY(-50%) rotateZ(-135deg);border-top:2px solid #dcddde;border-left:2px solid #dcddde;box-sizing:border-box}.episodes{display:flex;flex-wrap:wrap;margin:1rem}.episodes .episodes-results{width:45%;margin:1.5rem}.episodes .episodes-results img:hover{transform:none}.episodes .episodes-results .episodes-details p{margin:2rem 0}.episodes .episodes-results .episodes-details p span{color:#2196f3}.episodes .episodes-results .episodes-details .episode-overview{text-align:justify;font-size:1.5rem}.character{display:flex;justify-content:space-between;margin:2rem}.character h2{margin-bottom:2rem}.character .img-container-actor{width:30%}.character .character-info{width:60%}.navCharacter{width:25%;margin:3rem auto;display:flex;justify-content:space-between;font-weight:bold}.search-results{margin:2rem auto}.search-results h2{text-align:center;margin:2rem}.search-results div>div>img{width:250}.external{margin:2rem 0}.external svg{font-size:3.5rem;color:#fff}header{display:flex;background-color:#23272a;padding:2rem}@media (max-width: 450px){header{position:relative;padding:1rem}}

